@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
* {
    box-sizing: border-box;
}

input {
    all: unset;
    box-sizing: border-box;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
}

body {
    background-color: black;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 14px;
    color: white;
}

button {
    background-color: white;
    color: black;
}

a {
    text-decoration: none;
    color: inherit;
}

form {
    width: 100%;
}

.container {
    width: 100%;
    max-width: 320px;
    display: flex;
    flex-direction: column;
}

.formInput {
    width: 100%;
    padding: 10px 20px;
    border-radius: 20px;
    border: 1px solid black;
    text-align: center;
    background-color: white;
    color: black;
}

.formBtn {
    cursor: pointer;
    width: 100%;
    padding: 7px 20px;
    text-align: center;
    color: white;
    border-radius: 20px;
    background-color: #04aaff;
    cursor: pointer;
}

.cancelBtn {
    cursor: pointer;
    background-color: tomato;
}

footer {
    width: 100%;
    text-align: right;
    margin-top: 100px;
    padding-right: 100px;
}

.authContainer {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.auth__icon {
    margin-bottom: 30px;
}

.authInput {
    max-width: 320px;
    width: 100%;
    padding: 10px;
    border-radius: 30px;
    background-color: rgba(255, 255, 255, 1);
    margin-bottom: 10px;
    font-size: 12px;
    color: black;
}

.authSubmit {
    text-align: center;
    background: #04aaff;
    color: white;
    margin-top: 10px;
    cursor: pointer;
}

.authError {
    color: tomato;
    text-align: center;
    font-weight: 500;
    font-size: 12px;
}

.authSwitch {
    color: #04aaff;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 50px;
    display: block;
    font-size: 12px;
    text-decoration: underline;
}

.authBtns {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 320px;
}

.authBtn {
    cursor: pointer;
    border-radius: 20px;
    border: none;
    padding: 10px 0px;
    font-size: 12px;
    text-align: center;
    width: 150px;
    background: white;
}

.factoryForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.factoryInput__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 20px;
    width: 100%;
}

.factoryInput__input {
    flex-grow: 1;
    height: 40px;
    padding: 0px 20px;
    color: white;
    border: 1px solid #04aaff;
    border-radius: 20px;
    font-weight: 500;
    font-size: 12px;
}

.factoryInput__arrow {
    position: absolute;
    right: 0;
    background-color: #04aaff;
    height: 40px;
    width: 40px;
    padding: 10px 0px;
    text-align: center;
    border-radius: 20px;
    color: white;
}

.factoryInput__label {
    color: #04aaff;
    cursor: pointer;
}

.factoryInput__label span {
    margin-right: 10px;
    font-size: 12px;
}

.factoryForm__attachment {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.factoryForm__attachment img {
    height: 80px;
    width: 80px;
    border-radius: 40px;
}

.factoryForm__clear {
    color: #04aaff;
    cursor: pointer;
    text-align: center;
}

.factoryForm__clear span {
    margin-right: 10px;
    font-size: 12px;
}

.factoryInput__input__image {
    opacity: 0;
}

.factoryInput__input::-webkit-input-placeholder {
    color: white;
    opacity: 0.9;
}

.factoryInput__input:-ms-input-placeholder {
    color: white;
    opacity: 0.9;
}

.factoryInput__input::-ms-input-placeholder {
    color: white;
    opacity: 0.9;
}

.factoryInput__input::placeholder {
    color: white;
    opacity: 0.9;
}

.home__pangs {
    margin-top: 30px;
}

.nav-ul {
    display: flex;
    justify-content: center;
    margin: 30px;
}

.linkHome {
    margin-right: 10px;
}

.linkProfile {
    margin-left: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 12;
}

.linkProfileWord {
    margin-left: 20px;
    margin-top: 10px;
}

.profileForm {
    border-bottom: 1px solid rgba(255, 255, 255, 0.9);
    padding-bottom: 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.logOut {
    margin-top: 30px;
}

.formBtn {
    margin-top: 10px;
}

.router__container {
    max-width: 890;
    width: 100%;
    margin: 0 auto;
    margin-top: 80;
    display: flex;
    justify-content: center;
}

.pangEdit .formBtn {
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 5px;
}

.pang {
    margin-bottom: 40px;
    background-color: white;
    width: 100%;
    max-width: 320px;
    padding: 20px;
    border-radius: 20px;
    position: relative;
    display: flex;
    flex-direction: column;
    color: rgba(0, 0, 0, 0.8);
}

.pang h4 {
    font-size: 14px;
}

.pang img {
    right: -10px;
    top: 15px;
    position: absolute;
    border-radius: 10px;
    width: auto;
    height: 60px;
    margin-top: 10px;
}

.pang__actions {
    position: absolute;
    right: 20px;
    top: 8px;
}

.pang__actions span {
    cursor: pointer;
}

.pang__actions span:first-child {
    margin-right: 10px;
}

